.App {
  text-align: center;
  background-color: transparent;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.react-chatbot-kit-chat-container {
  width: 800px !important;
}

.react-chatbot-kit-chat-header {
  background-color: #3a5eb9 !important;
  color: white !important;
}

.react-chatbot-kit-chat-bot-avatar-container {
  position: absolute !important;
  position: absolute !important;
  top: 60% !important;
  left: 2% !important;
  -moz-transform: translateX(-50%) translateY(-50%) !important;
  -webkit-transform: translateX(-50%) translateY(-50%) !important;
  transform: translateX(-50%) translateY(-50%) !important;
}

.react-chatbot-kit-chat-bot-avatar-container svg {
  fill: white !important;

}

.react-chatbot-kit-chat-bot-message {
  text-align: justify !important;
  font-size: "0.85rem" !important;
  padding: "10px" !important;
  word-wrap: break-all !important;
}

.react-chatbot-kit-chat-bot-message-container {
  position: relative !important;
  margin: 10px 40px !important;
}

.react-chatbot-kit-chat-bot-avatar {
  position: absolute !important;

}

.react-chatbot-kit-user-chat-message-arrow {
  position: unset !important;
}

.react-chatbot-kit-user-chat-message-container {
  justify-content: flex-start !important;
  position: relative !important;
}

.react-chatbot-kit-chat-input-container {
  display: none !important;
}

.react-chatbot-kit-chat-header {
  display: none !important;
}

.react-chatbot-kit-chat-message-container {
  border: 1px solid !important;
  border-radius: 10px !important;
  overflow-y: scroll !important;
  overflow-x: auto !important;
  height: 500px !important;
}

.react-chatbot-kit-user-chat-message-container {
  display: none !important;
}

.app-chatbot-button {
  width: 60px !important;
  height: 60px !important;
  border-radius: 30px !important;
  padding: 10px !important;
  color: #fff !important;
  background-color: #3a5eb9 !important;
  border: none !important;
  position: fixed !important;
  z-index: 9999 !important;
  right: 25px !important;
  display: flex !important;
  flex-direction: column !important;
  align-items: center !important;
}

.app-chatbot-button-icon {
  fill: #fff !important;
}

.react-chatbot-kit-chat-input {
  background-color: #e3ebf3 !important;
}

.react-chatbot-kit-chat-btn-send {
  background-color: #e3ebf3 !important;

}

.react-chatbot-kit-chat-btn-send-icon {
  fill: #3a5eb9 !important;
}