.options-container {
  align-items: center;
}


.option-message-content {
  font-size: 0.60rem;
  padding: 5px;
  text-align: center;
  color: #54565a;
  white-space: pre-wrap;
  word-break: break-word;
  margin-left: 5px;
  font-weight: bold;
  display: inline-block;
  position: relative;
  width: 50%;

}

.option-button-positive:hover svg {
  fill: #8ea9eb;
}

.option-button-positive svg {
  fill: #3a5eb9;
}


.option-button-negative:hover svg {
  fill: #080808;
}

.option-button-negative svg {
  fill: #787b80;
}