.factsad-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.factsad-button-negative {
    margin: 1px;
    background: white;
    text-align: center;
    border: none;
    display: inline-block;
    transition-duration: 0.4s;
    transform: scaleX(-1);

}

.factsad-button-positive {
    margin: 1px;
    background: white;
    border: none;
    text-align: center;
    display: inline-block;
    transition-duration: 0.4s;
}

.factsad-message-content {
    font-size: 0.60rem;
    text-align: left;
    color: #54565a;
    white-space: pre-wrap;
    word-break: break-word;
    margin-left: 5px;
    font-weight: bold;
    display: inline-block;
    width: 50%;

}

.factsad-button-positive:hover svg {
    fill: #8ea9eb;
}

.factsad-button-positive svg {
    fill: #3a5eb9;
}


.factsad-button-negative:hover svg {
    fill: #080808;
}

.factsad-button-negative svg {
    fill: #787b80;
}